.Crud {
    padding: 1% 5% 1% 5%;
    width: 70%;
    background-color: #fff;
    margin: 15% auto;
  }
  .Crud h2 {
    text-align: center;
    padding: 20px 10px;
    color: #fff;
    background-color: rgb(243, 109, 109);
  }
  .Crud .list{
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  .Crud .list li{
    font-weight: 500;
    position: relative;
    padding:10px 10px 10px 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 10px;
    width: 100%;
  }
  
  .Crud .list button {
    all: unset;
    background-color: #333;
    color: white;
    border: none;
    position: absolute;
    right: 0;
    text-align: center;
    padding: .5% 2%;
    width: 18%;
    display: inline-block;
  }
  .Crud .list button:first-of-type {
    margin-right: 23%;
  }
  .Crud .list button:last-of-type {
    background-color: rgb(243, 109, 109);
  }
.Crud .update {
    width: 100%;
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
    margin: 10px 0;
    -webkit-align-items: center;
            align-items: center; 
}
.update input[type="text"] {
    outline: none   ;
    width: 78%;
    overflow: hidden;
    padding: 1.4% 2%;
    background-color: #ccc;
    border: none;
}
.update input[type="submit"] {
    all: unset;
    background-color: rgb(0, 161, 81);
    color: white;
    border: none;
    text-align: center;
    padding: .5% 2%;
    width: 18%;
    display: inline-block;
  }
